background-color: #7f00ff;
}
.imagebox img:hover{
  position: relative;
  height: 100%;
  width: 150%;
  top: -100px;
  right: 30px;
  margin-top: 200px;
  animation: animateUser 10s ease infinite;
  border: 2px solid white;
  border-radius: 50%;
}
.imagebox img{
  position: relative;
  height: 100%;
  width: 150%;
  top: -100px;
  right: 10px;
  /* left: 30%; */
  margin-top: 200px;
  
  border: 2px solid white;
  border-radius: 50%;
}
@keyframes animateUser {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.5, 1.5);
  }
  100% {
    transform: scale(1, 1);
  }

}
.imagebox #splash{
  position: absolute;
  top: 50%;
  left: 80%;
  transform: translate(-70%,-50%) rotate(-35deg);
  width: 400px;
  filter: saturate(200%);
  height: 400px;
  animation: animate 4s linear infinite;
}
@keyframes animate {
  50%{
    top: 49%;
  left: 79%;
  /* width: 155%; */
  }

}

/* Skills css */

.skillcont{
width: 80%;
background-color: black;
display: flex;
justify-content: space-around;
margin: auto;

}
.frontend{
  border: 2px solid red;
background-color: white;
display:grid;
grid-template-columns: repeat(3,1fr);
gap: 20;

width: 33%;
}
.backend{
  width: 33%;
  display:grid;
  grid-template-columns: repeat(2,1fr);
  gap: 20;
  border: 2px solid red;
  background-color: white;
  height: 300px;
  align-content: center;
  align-items: center;
}
.tools{
  width: 33%;
  display:grid;
  grid-template-columns: repeat(2,1fr);
  gap: 20;
  background-color: white;
  border: 2px solid red;
}
